// components/checkout/SummaryBox.tsx
"use client";

import { useState, useMemo, type ReactElement } from "react";
import { useCart } from "@/lib/cart-context";
import { getTotalPromo } from "@/lib/precio-producto";
import CostRow from "./CostRow";

/* -------------------------------------------------------------------------- */
/*                                  Helpers                                   */
/* -------------------------------------------------------------------------- */

/**
 * Calcula tarifas y sugerencia de propina en USD.
 *
 * - **Env√≠o:** gratuito a partir de \$40.  
 * - **Servicio:** 6 % sobre el subtotal (m√≠n. \$0.99).  
 * - **Propina sugerida:** 15 % sobre el subtotal.
 *
 * @param sub Subtotal de los productos (ya con promociones aplicadas).
 * @returns Objeto con `{ envio, servicio, tipSugg }`.
 */
function calcFeesUSD(sub: number) {
  const envio = sub >= 40 ? 0 : 4.99;
  const servicio = Math.max(+((sub * 0.06).toFixed(2)), 0.99);
  const tipSugg = +((sub * 0.15).toFixed(2));
  return { envio, servicio, tipSugg };
}

/* -------------------------------------------------------------------------- */
/*                                  Tipos                                     */
/* -------------------------------------------------------------------------- */

/** Props esperadas por `SummaryBox`. */
interface Props {
  /** Callback al padre cuando el pedido se ha creado. */
  onOrderPlaced: (id: string) => void;
}

/* -------------------------------------------------------------------------- */
/*                               Componente UI                                */
/* -------------------------------------------------------------------------- */

/**
 * **SummaryBox**
 *
 * Muestra el desglose de costos y permite finalizar el pedido.
 * Al hacer clic en ‚ÄúHacer pedido‚Äù:
 * 1. Genera un ID √∫nico (`ORD-<timestamp>`).
 * 2. Persiste el pedido en `localStorage`.
 * 3. Vac√≠a el carrito y notifica al componente padre.
 */
export default function SummaryBox({ onOrderPlaced }: Props): ReactElement {
  const { items, clear } = useCart();

  /* ------------------------------ Subtotal ------------------------------ */
  const subtotal = useMemo(
    () =>
      items.reduce(
        (sum, it) =>
          sum + getTotalPromo(it.promo, it.price, it.quantity).total,
        0,
      ),
    [items],
  );

  /* ------------------------- Tarifas y propina ------------------------- */
  const { envio, servicio, tipSugg } = useMemo(
    () => calcFeesUSD(subtotal),
    [subtotal],
  );

  const [tip, setTip] = useState<number>(tipSugg);
  const total = subtotal + envio + servicio + tip;

  /* --------------------------- Crear pedido ---------------------------- */
  const handlePlaceOrder = () => {
    if (items.length === 0) return;

    const id = "ORD-" + Date.now().toString(36).toUpperCase();

    const order = {
      id,
      date: new Date().toISOString(),
      items,
      fees: { envio, servicio, tip },
      subtotal,
      total,
    };

    const prev = JSON.parse(localStorage.getItem("orders") ?? "[]");
    localStorage.setItem("orders", JSON.stringify([...prev, order]));

    clear(); // vaciar carrito
    onOrderPlaced(id);
  };

  /* ------------------------------ Render ------------------------------- */
  return (
    <div className="bg-[#2a2a2a] rounded-xl p-6 shadow-md h-fit text-white">
      <h3 className="font-semibold text-lg mb-4">Resumen</h3>

      <div className="space-y-2">
        <CostRow label="Costo de productos" value={subtotal} />
        <CostRow label="Costo de env√≠o" value={envio} />
        <CostRow label="Tarifa de servicio" value={servicio} />

        {/* Propina editable */}
        <div className="flex justify-between items-center text-sm">
          <span className="text-gray-300">Propina</span>
          <select
            className="bg-[#2a2a2a] border border-gray-600 rounded px-2 py-0.5 focus:outline-none"
            value={tip}
            onChange={(e) => setTip(+e.target.value)}
          >
            {[0, 0.05, 0.1, 0.15, 0.2].map((p) => (
              <option key={p} value={+(subtotal * p).toFixed(2)}>
                {p === 0 ? "0 %" : `${p * 100}%`} (
                {p === 0 ? "$0.00" : `$${(subtotal * p).toFixed(2)}`})
              </option>
            ))}
          </select>
        </div>

        <hr className="border-gray-600 my-3" />

        <div className="flex justify-between font-bold text-lg">
          <span>Total</span>
          <span>${total.toFixed(2)}</span>
        </div>
      </div>

      <button
        disabled={items.length === 0}
        onClick={handlePlaceOrder}
        className="mt-6 w-full py-3 rounded-xl font-semibold transition
                   bg-[#00c853] hover:bg-[#00b248] disabled:opacity-40"
      >
        Hacer pedido
      </button>
    </div>
  );
}
